import { Component, OnInit } from '@angular/core';
import { Feedback } from '../Feedback';
import { NewsService } from '../news.service';


@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  feedback : Feedback
  email : string = '';
  whoAreYou : string = '';
  concern : string = '';

  header : string;
  body1 : string;
  body2 : string;
  

  constructor(private _newsservice : NewsService) { }

  ngOnInit(): void {
  }

  buttonActive(){
    if(this.email == '' || this.whoAreYou == '' || this.concern == ''){
      return true      
    }
    else{
      return false
    }
  }

  onSubmit(){
    // if(this.email==''){
    //   alert('email empty')
    // }
    
    if(this.email==''||this.whoAreYou==''||this.concern==''){
      this.header = 'Data Required';
      this.body1 = 'Please Enter all the details in the form.';
      this.body2 = '';
    }else{
      this.header = 'Thank You!!';
      this.body1 = 'Your Response have been recorded.';
      this.body2 = 'We will be contacting you soon.';

      this.feedback = {
        email : this.email,
        whoAreYou : this.whoAreYou,
        concern : this.concern
      }
      //alert(this.email)
      this._newsservice.postFeedback(this.feedback).subscribe();
      this.email = this.whoAreYou = this.concern = ''
    }

    
  }

}
