import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { News } from './News';
import { Feedback } from './Feedback';

@Injectable({
  providedIn: 'root'
})
export class NewsService {

  //private _baseUrl = "http://localhost:8080/"

  constructor(private _http : HttpClient) { }

  getNews(){
    return this._http.get<{message : string , news:any}>("news")
  }
  
  getTechNews(){
    return this._http.get<{message : string , technews:any}>("technews")
  }

  postFeedback(feedback : Feedback){
    //alert(feedback.email+feedback.concern+feedback.whoAreYou)
    return this._http.post<{message : string,feedback:any}>("feedback",feedback)
  }
}
