import { Component, OnInit } from '@angular/core';
import {News} from '../News';
import { NewsService } from '../news.service';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  technewslist : News[]
  technews : News
  loader : boolean = true
  
  overlay(){
    setTimeout(() => {
      this.loader = false
    }, 4000);
  }
  

  constructor(private _newsservice : NewsService) { }

  ngOnInit(): void {
    
    this.loader = true
   
    this._newsservice.getTechNews().pipe(map(responseData=>{
      return responseData.technews.articles.map(technews=>{
        return {
          author : technews.author,
          title : technews.title,
          description : technews.description,
          url : technews.url,
          urlToImage : technews.urlToImage,
          publishedAt : technews.publishedAt
        }
      })
    })).subscribe(changedData=>{
      this.technewslist = changedData
    })
  }

}
